.skill {
    display: block;
    border-radius: 5px;
    border: 1px solid var(--post-border);
    padding: 15px 20px;
    margin-bottom: 20px;
    text-decoration: none;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.skill:hover {
    border: 1px solid var(--post-border-hover);
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.skill>.header {
    width: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    margin-bottom: 10px;
    display: -ms-flexbox;
    display: flex;
}

.skill>.bar {
    width: 100%;
    height: 8px;
    overflow: hidden;
    border-radius: 4px;
    background-color: var(--progress-bar-background);
}

.bar>div {
    height: 100%;
    border-radius: 4px;
    background-color: var(--post-border-hover);
}


